<template>
  <section class="home">
    <Loading/>
    <Toast :show="$bus.toast.show"/>
    <section class="main">
      <ff-header/>
      <div class="inner">
        <Pool class="home-pool"/>
        <ff-main class="home-main" :class="$bus.firstLoading ? 'hidden' : ''" :shuffle="$bus.shuffleAnime" :flipAnime="$bus.flipAnime"/>
      </div>
    </section>
    <Confirm/>
    <Reward/>
    <Rule/>
    <History/>
  </section>
</template>
<script>
import { setSessionStorage, getSessionStorage, initGA, addGA, errorGA } from '@/utils/assist'
import Toast from '@/components/common/Toast.vue'
import Loading from '@/components/common/Loading.vue'
import ffHeader from '@/components/common/Header.vue'
import Pool from '@/components/pool/'
import ffMain from '@/components/main/'
import Confirm from '@/components/confirm/'
import Rule from '@/components/rule/'
import History from '@/components/history/'
import Reward from '@/components/reward/'
export default {
  name: 'home',
  data () {
    return {
      globalConfig: this.$bus.$data // 全局被观测数据对象
    }
  },
  components: {
    Loading,
    Toast,
    ffHeader,
    Pool,
    ffMain,
    Confirm,
    Rule,
    History,
    Reward
  },
  methods: {
    initInfo () {
      let accessToken = getSessionStorage(`${PROJECT_NAME}AccessToken`) || ''
      let lang = getSessionStorage(`${PROJECT_NAME}Lang`) || ''
      if (!accessToken) return this.$bus.handleError('TOAST_LOGIN_FAILED')
      this.$api
        .info({
          access_token: accessToken,
          lang: lang
        })
        .then(response => {
          let data = response.data
          this.$bus.uid = data.player.uid
          this.$bus.gems = data.player.gems
          this.$bus.transify = data.transify
          this.$bus.rule.content = data.common_config.rule
          this.$bus.setRewardPool(data.rewards)
          this.$bus.setActInfo(data.player, data.common_config)
          if (data.player.init) {
            localStorage.removeItem(`${PROJECT_NAME}_load_${this.$bus.uid}`)
            localStorage.removeItem(`${PROJECT_NAME}_flipIdxs_${this.$bus.uid}`)
          }
          this.initNotice()
          document.body.setAttribute('data-lang', data.common_config.lang)
          initGA(data.common_config.region, this.$bus.uid)
        })
    },
    initNotice () {
      let flipNotice = localStorage.getItem(`${PROJECT_NAME}_flip_${this.$bus.uid}`)
      let refreshNotice = localStorage.getItem(`${PROJECT_NAME}_refresh_${this.$bus.uid}`)
      let skipAnime = localStorage.getItem(`${PROJECT_NAME}_skip_${this.$bus.uid}`)
      let firstLoad = localStorage.getItem(`${PROJECT_NAME}_load_${this.$bus.uid}`)
      this.$bus.flipNotice = flipNotice !== 'false'
      this.$bus.refreshNotice = refreshNotice !== 'false'
      this.$bus.skipAnime = skipAnime === 'true'
      this.$bus.firstLoad = firstLoad !== 'false'
    }
  },
  mounted () {
    this.initInfo()
  }
}
</script>
<style lang="scss" scoped>
.home {
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position-y: top;
  .main {
    width: 100%;
    height: 100%;
    @include flexcc;
  }
}
.inner {
  position: relative;
  width: 100%;
  @include flexrc;
  @include px2res(margin-top, 72);
}
.home-pool {
  float: left;
  @include px2res(margin-top, -8);
}
.home-main.hidden {
  opacity: 0;
}
</style>



